<template>
    <v-page>
        <div slot="header">
            <v-header>
                <v-btn></v-btn>
                <h1>快速注册</h1>
            </v-header>
        </div>
        <div class="result-container">
            <div class="top">
                <i class="tran-result-icon succ"></i>
                <p class="tran-status">注册成功</p>
                <p class="tran-tips">
                    您已完成注册，需要添加银行卡号才可以在手机银行进行交易！
                </p>
            </div>
            <div class="cell-wrap">
                <v-cell-box>
                    <span class="title" slot="left">银行卡号</span>
                    <input type="number"
                           placeholder="请输入您的银行卡号"
                           class="input-box"
                           slot="middle"
                           v-model="regInfo.bankCardNo"
                    >
                </v-cell-box>
            </div>
            <div class="btn-wrap">
                <v-linear-button btnTxt="下一步" :disabled="!canNext" @click="jumpAddCard"></v-linear-button>
            </div>
        </div>
        <v-toast :toastTxt="errorMsg.networkTimeout" v-model="showToast"></v-toast>
    </v-page>
</template>

<script>
    import {vCellBox} from '../../component/cell'
    import {vLinearButton} from '../../component/linearButton'
    import {vToast} from '../../component/toast'
    import { errorMsg } from '../../dictionary'
    export default {
        name: 'quickRegResult',
        components: {
            vCellBox,
            vLinearButton,
            vToast
        },
        computed: {
            canNext () {
                return this.regInfo.bankCardNo !== ''
            },
            errorMsg () {
                return errorMsg
            }
        },
        data () {
            return {
                // 注册信息
                regInfo: {
                    // 银行卡号
                    bankCardNo: ''
                },
                // 是否显示toast提示框
                showToast: false
            }
        },
        methods: {
            // 显示toast提示
             handleShowToast (errMsg) {
                 this.showToast = true
             },
            // 跳转到添加卡页面
            jumpAddCard () {
                let jumpUrl = '/login/addBankCard/addBankCard'
                window.T.go(jumpUrl)
            }
        }
    }
</script>

<style lang="scss" scoped>
    .result-container{
        display: flex;
        flex-direction: column;
        font-family: PingFangSC-Regular;
        padding-bottom: px2rem(20);
        .top {
            border-top: 1px solid #DBDBDB;
            padding: px2rem(58) px2rem(28) px2rem(54) px2rem(28);
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: px2rem(20);
            background-color: #ffffff;
            .tran-result-icon {
                height: px2rem(150);
                width: px2rem(150);
                margin-bottom: px2rem(30);
                &.succ {
                    background: url("./images/tran-succ.png") no-repeat center;
                    background-size: 100%;
                }
                &.fail {
                    background: url("./images/tran-fail.png") no-repeat center;
                    background-size: 100%;
                }
                &.apply {
                    background: url("./images/tran-apply.png") no-repeat center;
                    background-size: 100%;
                }
            }
            .tran-status {
                color: $font-black;
                font-size: px2rem(44);
                line-height: px2rem(60);
                font-family: PingFangSC-Regular;
                margin-bottom: px2rem(26);
            }
            .tran-tips {
                max-width: px2rem(452);
                color: #9B9B9B;
                font-size: px2rem(24);
                line-height: px2rem(42);
                text-align: center;
            }
        }
        .cell-wrap {
            font-size: px2rem(32);
            span {
                display: block;
            }
            .title {
                width: px2rem(160);
                margin-right: px2rem(50);
                line-height: px2rem(44);
            }
            .info {
                line-height: px2rem(36);
            }
            input {
                width: 100%;
                font-size: px2rem(32);
            }
        }
        .btn-wrap{
            padding: 0 px2rem(40);
            margin-top: px2rem(496);
        }
    }

</style>



